import React, { Component } from "react";
import MetaHead from "../../components/MetaHead";
import WebHeadForWorkPage from "../../components/WebHeadForWorkPage";
import { data } from "../data.json";
import Label from "../../components/Label";
import { GH_PAGE_URL } from "../../constants";
import CodeSection from "../../components/CodeSection";
import FacebookComment from "../../components/FacebookComment";
import {
  Layout,
  ArticleSubTitle,
  ArticleTitle,
  Author,
  MainPhoto,
  DateAndLabels
} from "./work-css";

class Work extends Component {
  static async getInitialProps(context) {
    const { id } = context.query;
    const articleData = data.find(articleData => articleData.id === id);
    return { articleData };
  }

  render() {
    const {
      title,
      subtitle,
      author,
      date,
      labels,
      url
    } = this.props.articleData;
    const href = `${GH_PAGE_URL}/work/${url}`;
    return (
      <div>
        <MetaHead title={`${title} | 工作元件筆記區`} />
        {console.log("目前網址:", href)}
        <WebHeadForWorkPage />
        <Layout>
          <ArticleTitle>{title}</ArticleTitle>
          <ArticleSubTitle>
            {subtitle}
            <iframe
              src={`https://www.facebook.com/plugins/share_button.php?href=${href}&layout=button_count&size=small&width=92&height=20&appId`}
              width="92"
              height="20"
              style={{
                border: "none",
                overflow: "hidden",
                marginLeft: "5px"
              }}
              scrolling="no"
              frameBorder="0"
              allowtransparency="true"
              allow="encrypted-media"
            />
          </ArticleSubTitle>
          <Author>
            <div className="author-image" />
            <div className="author-name-and-labels">
              <p className="author-name">{author}</p>

              <DateAndLabels>
                <span className="date">{date}</span>
                {labels.map((label, i) => (
                  <Label
                    key={i}
                    name={label.labelName}
                    type={label.labelCode}
                  />
                ))}
              </DateAndLabels>
            </div>
          </Author>
        </Layout>
        <MainPhoto />
        <Layout>
          透過不同的網站
          <CodeSection>{`import React, { Component } from "react";
import { Dialog, Button } from "dawn-react-ui-lib";

class Example extends Component{
    state={
      open: false
    }
    // 使用者自行定義的Button的點擊事件
    openDialog = () => {
      this.setState({
        open:true
      })
    }
    // 傳下去給Dialog關閉畫面的事件
    closeDialog = () => {
      this.setState({
        open:false
      })
    }
    render(){
        return(
          <div>
            <Button
              text="用戶確認對話框"
              onClick={this.openDialog}
              width="250px"
              padding="16px"
            />
            <Dialog
              open={this.state.open}
              handleClose={this.closeDialog}
              contentHeader="標題文字"
              contentBody={<div>內容</div>}
              userAgree
              userAgreeFunc={() => {
                alert("自定義確認事件");
              }}
            />
          </div>
        )
    }
}

export default Example;`}</CodeSection>
          <FacebookComment href={href} />
        </Layout>
      </div>
    );
  }
}

export default Work;
